body {
  background-color: #212529;
}

.AppDefault {
  color: #d3d3d3;
  margin-top: 20vmin;
  justify-content: center;
  align-items: center;
}

.Title {
  margin: 10vmin 0 10vmin 0;
}

.centerText {
  text-align: center;
}


/* Navbar */

.navbg {
  background-color: #000;
}
.navbar {
  height: 10vmin;
}

.navbar a {
  background-image: linear-gradient(to right, #8ab4f8, #8ab4f8 50%, #fff 50%);
  background-size: 200% 100%;
  background-position: -100%;
  display: inline-block;
  padding: 5px 0;
  position: relative;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.5s ease;
  font-size: 18px;
}

.navbar a:last-child {
  padding-right: 10px;
}

.navbar a:before {
  content: "";
  background: #8ab4f8;
  display: block;
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 3px;
  transition: all 0.3s ease-in-out;
}

.navbar a:hover {
  background-position: 0;
}

.navbar a:hover::before {
  width: 100%;
}


/* Hero Banner */


.heroBanner img {
  border-radius: 50%;
  border-style: groove;
  border-width: 1px;
  border-color: #fff;

}

.heroBanner h3 {
  font-size: 50px;
}

.heroBanner p {
  font-size: 30px;
}

/* Footer */

.footer {
  margin-top: 10vmin;
  bottom: 0;
  position: relative;
 text-align: center;
width: 100%;
padding: 0px 0px 0px 0px;
background-color: #000;


}
